import Hexagon from 'react-hexagon';
import { Box, Column } from 'gestalt';
import 'gestalt/dist/gestalt.css';
import './index.css';

class Index extends React.Component{
  constructor(props) {
    super(props)
    this.state = {
      size: 5,
      strokeWidth: 21,
      chooseEl: [],
      arrayEl: [],
      currentPlayer: "#0080ff",
      winner: null
    }
  }

  choose(i, j) {
    if(i!== 0 && j !== 0 && i !== 12 && j !== 12)
      this.setState({
        chooseEl: [i, j]
      })
  }

  play(i, j) {
    if(i!== 0 && j !== 0 && i !== 12 && j !== 12 && !(this.state.arrayEl[i] && this.state.arrayEl[i][j])){
      this.setState(prev => {
        if(!prev.arrayEl[i]) prev.arrayEl[i] = [];
        prev.arrayEl[i][j] = prev.currentPlayer;
        let array = [];
        
        function checkWin(c)
        {
            var open = [], openPrev = [], closed = [], closedPrev = [];
            for(var a=0; a<14; a++)
            {
                if(board[c==0?a:0][c==0?0:a] == c)
                {
                    open.length = openPrev.length = closed.length = closedPrev.length = 0;
                    var pathFound = false;
                    open.push([c==0?a:0, c==0?0:a]);
                    openPrev.push(-1);
                    while(open.length > 0)
                    {
                        var u = open[0];
                        open.splice(0, 1);
                        var uI = openPrev.splice(0, 1);
                        closed.push(u);
                        closedPrev.push(uI);
                        if(u[c==0?1:0] == 13)
                        {
                            pathFound = true;
                            break;
                        }
                        var connections = getConnections(u[0], u[1], c, open, closed);
                        for(var i=0; i<connections.length; i++)
                        {
                            open.push(connections[i]);
                            openPrev.push(closed.length-1);
                        }
                    }
                    if(pathFound)
                    {
                        var path = [];
                        var u = closed.length-1;
                        while(closedPrev[u] != -1)
                        {
                            path.push(closed[u]);
                            u = closedPrev[u];
                        }
                        path.push([c==0?a:0, c==0?0:a]);
                        path.reverse();
                        active = false;
                        return path;
                    }
                }
            }
            return false;
        }

        return {
          arrayEl: prev.arrayEl,
          currentPlayer: prev.currentPlayer === "#0080ff" ? "#e50000":"#0080ff"
        }
      })

    }
  }

  row = (j) => {
    let row = [];
    for(let i = 0; i <= 12; i ++) {
      if(!((i == 0 && j == 0) || (i == 12 && j == 12) || (i == 0 && j == 12) || (i == 12 && j == 0)))
        row.push(
          <Hexagon
            style={{
              stroke: (i == 0 || i == 12) ? "#e50000" : (j == 0 || j == 12) ? "#0080ff" : '#ffffff', 
              strokeWidth: this.state.strokeWidth,
              fill: 
                (this.state.arrayEl[i] && this.state.arrayEl[i][j])? this.state.arrayEl[i][j] : (i == this.state.chooseEl[0] && j == this.state.chooseEl[1]) ? 'rgba(255, 255, 255, .8)' : ((j == 0 || j == 12) || (i == 0 || i == 12)) ? "transparent" : 'rgba(255, 255, 100, .15)'
            }}
            key={"tile" + i + j}
            onClick={e => {this.play(i, j)}}
            hexProps={{
              onMouseLeave: e => {e.type === "mouseenter" ? this.choose(i, j) : {}},
              onMouseEnter: e => {e.type === "mouseenter" ? this.choose(i, j) : {}},
            }}
          >
            {
              !(i!== 0 && j !== 0 && i !== 12 && j !== 12) ?
              <text x="41%" y="60%">{(i == 0) ? j : (j == 12) ? String.fromCharCode(i + 64): ""}</text> : <text></text>
            }
          </Hexagon>
        )
      else row.push(<Box width={this.state.size + "%"} key={"tile" + i + j}></Box>)
    }
    return row
  }

  table = () => {
    let table = [];
    for(let i = 0; i <= 12; i ++) {
      table.push(
        <Box 
          display="flex" 
          direction="row" 
          dangerouslySetInlineStyle={{__style:{
            width: "100%", 
            marginLeft: i*(this.state.size/2) + "%", // true
            marginTop: i !== 0 ? "-" + ((this.state.size)/(2*Math.sqrt(3)) + 0.01) +"%" : "0", 
          }}} 
          key={"row" + i}>
          {
            this.row(i)
          }
        </Box>
      )
    }
    return table
  }

  render() {
    return (
    <Box width="100%" height="100vh" display="flex">
      <Column span={9}>
        <Box display="flex" justifyContent="center" alignItems="center" alignContent="center" height="100vh" color="transparentDarkGray">
          <Box 
          position="relative"
          dangerouslySetInlineStyle={{__style:{
            left: "2.5%",
          }}} >
            { this.table() }
          </Box>
        </Box>
      </Column>
      <Column span={3}>
      </Column>
    </Box>)
  }
}

export default Index